        },
        methods: {
            img(good) {
                return good.pic;
            },
            addCount(good) {
                good.count = good.count + 1;
                this.$axios.put('/api/changecart/',{
                                buyer: this.id,
                                good: good.goodId,
                                count: good.count
                    }).then((res) => {
                    });
                this.cal();
            },
            minCount(good) {
                if (good.count > 1) {
                    good.count = good.count - 1;
                    this.$axios.put('/api/changecart/',{
                                buyer: this.id,
                                good: good.goodId,
                                count: good.count
                    }).then((res) => {
                    });
                    this.cal();
                }
            },
            del(good) {
                this.$axios({
                    method: 'delete',
                    url: '/api/deletecart/' + this.id + '/' + good.goodId + '/',
                }).then(res => {
                    this.flashWeb();
                });
                this.$notify({
                    title: '成功',
                    message: '成功从购物车内移除',
                    type: 'success'
                });
            },
            async flashWeb() {
                await axios.get('/api/getcart/' + this.id + '/').then((res) => {
                this.goods = res.data;           
                });
                this.cal();
            },
            back() {
                this.$router.push({path:'/main',query: {userId: this.id} });     
            },
            cal() {
                let result = 0;
                for (var i = 0;i < this.goods.length;i++) {
                    result = result + this.goods[i].count*this.goods[i].price;
                }   
                this.total = result;
            },
            click_detail(good) {
                let routerDate = this.$router.resolve({
                    path: '/detail',
                    query: {
                            id: good.goodId,
                            username: this.id
                            }
                });
                window.open(routerDate.href,'_blank');
            },
            buy1(good) {
                this.buyVisible=true;
                this.curGood = good;
            },
            async buy() {
                this.buyVisible=false;
                console.log(this.buyAddr[0]);
                console.log(this.buyAddr[1]);
                let result = 0;
                await this.$axios({
                    method: 'post',
                    url: '/api/buy/',
                    data: JSON.stringify({
                            count:this.curGood.count,
                            ToPlace: this.buyAddr[0]+this.buyAddr[1],
                            good:this.curGood.goodId,
                            buyer:this.id,
                            ticket:-1
                            })
                    }).then(res => {
                        this.$notify({
                            title:'购买成功',
                            message:'已经购买成功',
                            type:'success'
                        });
                        result = 1;
                    }).catch((err) => {
                        if (err.response) {
                                this.$notify({
                                    title:'购买失败',
                                    message:'钱包里没有足够的米',
                                    type:'error'
                            });
                        }
                    });
                if (result == 1) {
                    await this.$axios.delete('/api/deletecart/' + this.id + '/' + this.curGood.goodId + '/').then(res => {
                    });
                }
                this.flashWeb();
            }
        },
        filters:{
                numFilter (value) {
                    let real = parseFloat(value).toFixed(2);
                    return real;
                }
            }
    }
</script>

